"use client";

import { useState } from "react";
import { Info, X, User, Sparkles, Users, Settings } from "lucide-react";
import { SearchDialog } from "./components/search-dialog";
import { PurchaseCreditsDialog } from "./components/purchase-credits-dialog";
import { InviteTeamDialog } from "./components/invite-team-dialog";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { Button } from "@/components/ui/button";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";

export default function DashboardPage() {
  const [showAlert, setShowAlert] = useState(true);

  return (
    <div className="w-full h-full">
      {showAlert && (
        <Alert className="w-full rounded-none border-x-0 border-t-0">
          <div className="flex items-center justify-between w-full">
            <div className="flex items-center gap-2">
              <Info className="h-4 w-4" />
              <AlertDescription>
                Velkommen til ditt nye dashboard! Her kan du administrere alle
                dine opptak.
              </AlertDescription>
            </div>
            <Button
              variant="ghost"
              size="icon"
              className="h-6 w-6"
              onClick={() => setShowAlert(false)}
            >
              <X className="h-4 w-4" />
            </Button>
          </div>
        </Alert>
      )}
      <div className="border-b">
        <div className="flex h-16 items-center px-4">
          <div className="flex-1" />
          <div className="flex items-center justify-center flex-1">
            <SearchDialog />
          </div>
          <div className="flex items-center justify-end gap-4 flex-1">
            <PurchaseCreditsDialog />
            <InviteTeamDialog />
            <DropdownMenu>
              <DropdownMenuTrigger asChild>
                <Button
                  variant="ghost"
                  className="relative h-8 w-8 rounded-full"
                >
                  <Avatar className="h-8 w-8">
                    <AvatarImage src="/avatars/01.png" alt="@user" />
                    <AvatarFallback>U</AvatarFallback>
                  </Avatar>
                </Button>
              </DropdownMenuTrigger>
              <DropdownMenuContent className="w-56" align="end" forceMount>
                <DropdownMenuLabel className="font-normal">
                  <div className="flex flex-col space-y-1">
                    <p className="text-sm font-medium leading-none">
                      User Name
                    </p>
                    <p className="text-xs leading-none text-muted-foreground">
                      user@example.com
                    </p>
                  </div>
                </DropdownMenuLabel>
                <DropdownMenuSeparator />
                <DropdownMenuItem>
                  <User className="mr-2 h-4 w-4" />
                  <span>Profil</span>
                </DropdownMenuItem>
                <DropdownMenuItem>
                  <Settings className="mr-2 h-4 w-4" />
                  <span>Innstillinger</span>
                </DropdownMenuItem>
                <DropdownMenuSeparator />
                <DropdownMenuItem>
                  <span>Logg ut</span>
                </DropdownMenuItem>
              </DropdownMenuContent>
            </DropdownMenu>
          </div>
        </div>
      </div>
      <div className="p-4">
        <div className="space-y-6">{/* Main content will go here */}</div>
      </div>
    </div>
  );
}
